
































import Vue from "vue";
import {letter, user} from "./database";
 const queryString = window.location.search;
 const urlParams = new URLSearchParams(queryString);

 export default Vue.extend({
   data: () => ({
     letter,
     user,
   }),
   computed: {
     getCity(){
       return urlParams.get('city');
     },
     getName(){
       return urlParams.get('name');
     }
   }
 });
